@import "./variables.scss";
@import "./partials/menu.scss";
@import "./partials/navbar.scss";
@import "./partials/main-panel.scss";
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
}
body {
    min-height: 100vh;
    width: 100%;
    background-color: $bg-color;
    display: flex;
    &::-webkit-scrollbar {
        display: none;
    }
}
